import React from 'react';
import { Formik, Form, Field, ErrorMessage } from 'formik';
import { registerSchema, RegisterFormData } from '@/schemas/registerSchema';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { User, Phone } from 'lucide-react';
import { toast } from 'sonner';

const RegisterForm = () => {


    const initialValues: RegisterFormData = {
        name: '',
        email: '',
        phone: '',
        role: 'patient',
        location: '',
    };

    const handleSubmit = async (values: RegisterFormData, { setSubmitting, resetForm }: any) => {
        try {
            console.log('Registration data:', values);

            // Simulate API call
            await new Promise(resolve => setTimeout(resolve, 1500));

            toast({
                title: "Registration Successful!",
                description: `Welcome to MedConnect, ${values.name}! Your ${values.role} account has been created.`,
                className: "bg-medical text-primary-foreground",
            });

            resetForm();
        } catch (error) {
            toast({
                title: "Registration Failed",
                description: "There was an error creating your account. Please try again.",
                variant: "destructive",
            });
        } finally {
            setSubmitting(false);
        }
    };

    return (
        <Card className="w-full max-w-md mx-auto shadow-lg border-0 bg-card/95 backdrop-blur-sm">
            <CardHeader className="text-center space-y-2">
                <div className="mx-auto w-16 h-16 bg-medical/10 rounded-full flex items-center justify-center mb-4">
                    <User className="w-8 h-8 text-medical" />
                </div>
                <CardTitle className="text-2xl font-bold text-medical">Join MedConnect</CardTitle>
                <CardDescription className="text-muted-foreground">
                    Create your account to connect with healthcare professionals
                </CardDescription>
            </CardHeader>

            <CardContent>
                <Formik
                    initialValues={initialValues}
                    validationSchema={registerSchema}
                    onSubmit={handleSubmit}
                >
                    {({ isSubmitting, setFieldValue, values }) => (
                        <Form className="space-y-4">
                            <div className="space-y-2">
                                <Label htmlFor="name" className="text-sm font-medium text-foreground">
                                    Full Name
                                </Label>
                                <Field
                                    as={Input}
                                    id="name"
                                    name="name"
                                    placeholder="Enter your full name"
                                    className="transition-all duration-200 focus:ring-2 focus:ring-medical/50"
                                />
                                <ErrorMessage name="name" component="div" className="text-sm text-destructive animate-slide-in" />
                            </div>

                            <div className="space-y-2">
                                <Label htmlFor="email" className="text-sm font-medium text-foreground">
                                    Email Address
                                </Label>
                                <Field
                                    as={Input}
                                    id="email"
                                    name="email"
                                    type="email"
                                    placeholder="Enter your email"
                                    className="transition-all duration-200 focus:ring-2 focus:ring-medical/50"
                                />
                                <ErrorMessage name="email" component="div" className="text-sm text-destructive animate-slide-in" />
                            </div>

                            <div className="space-y-2">
                                <Label htmlFor="phone" className="text-sm font-medium text-foreground">
                                    Phone Number
                                </Label>
                                <div className="relative">
                                    <Phone className="absolute left-3 top-1/2 transform -translate-y-1/2 w-4 h-4 text-muted-foreground" />
                                    <Field
                                        as={Input}
                                        id="phone"
                                        name="phone"
                                        placeholder="+1 (555) 123-4567"
                                        className="pl-10 transition-all duration-200 focus:ring-2 focus:ring-medical/50"
                                    />
                                </div>
                                <ErrorMessage name="phone" component="div" className="text-sm text-destructive animate-slide-in" />
                            </div>

                            <div className="space-y-2">
                                <Label htmlFor="role" className="text-sm font-medium text-foreground">
                                    Role
                                </Label>
                                <Select
                                    value={values.role}
                                    onValueChange={(value) => setFieldValue('role', value)}
                                >
                                    <SelectTrigger className="transition-all duration-200 focus:ring-2 focus:ring-medical/50">
                                        <SelectValue placeholder="Select your role" />
                                    </SelectTrigger>
                                    <SelectContent>
                                        <SelectItem value="patient" className="cursor-pointer">
                                            <div className="flex items-center space-x-2">
                                                <div className="w-2 h-2 bg-blue-500 rounded-full"></div>
                                                <span>Patient</span>
                                            </div>
                                        </SelectItem>
                                        <SelectItem value="doctor" className="cursor-pointer">
                                            <div className="flex items-center space-x-2">
                                                <div className="w-2 h-2 bg-medical rounded-full"></div>
                                                <span>Doctor</span>
                                            </div>
                                        </SelectItem>
                                    </SelectContent>
                                </Select>
                                <ErrorMessage name="role" component="div" className="text-sm text-destructive animate-slide-in" />
                            </div>

                            <div className="space-y-2">
                                <Label htmlFor="location" className="text-sm font-medium text-foreground">
                                    Location
                                </Label>
                                <Field
                                    as={Input}
                                    id="location"
                                    name="location"
                                    placeholder="City, State or Country"
                                    className="transition-all duration-200 focus:ring-2 focus:ring-medical/50"
                                />
                                <ErrorMessage name="location" component="div" className="text-sm text-destructive animate-slide-in" />
                            </div>

                            <Button
                                type="submit"
                                disabled={isSubmitting}
                                className="w-full bg-medical hover:bg-medical-dark text-primary-foreground font-medium py-2.5 transition-all duration-200 transform hover:scale-[1.02] disabled:opacity-70 disabled:cursor-not-allowed disabled:hover:scale-100"
                            >
                                {isSubmitting ? (
                                    <div className="flex items-center space-x-2">
                                        <div className="w-4 h-4 border-2 border-primary-foreground border-t-transparent rounded-full animate-spin"></div>
                                        <span>Creating Account...</span>
                                    </div>
                                ) : (
                                    'Create Account'
                                )}
                            </Button>
                        </Form>
                    )}
                </Formik>
            </CardContent>
        </Card>
    );
};

export default RegisterForm;